<style scoped lang="less">
    .page {
        text-align: left;
        padding: 30px;
    }

    .pagination {
        margin-top: 20px;
        width: 100%;
        text-align: center;
    }

    .num-input {
        width: 100px;
    }

</style>

<template>
    <div class="page">
        <div>
            <el-row class="header">
                <el-col class="filter-label" :span="2">规则编号:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.id" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-label" :span="2"> 状态:</el-col>

                <el-col :span="4">
                    <el-select class="filter-match" v-model="filter.status" placeholder="请选择">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-col>
                <el-col class="filter-label" :span="2">角色名称:</el-col>
                <el-col :span="4">
                    <el-input class="filter-match" v-model="filter.name" placeholder="请输入"></el-input>
                </el-col>
                <el-col class="filter-btn" :span="6">
                    <el-button icon="el-icon-search" type="primary" @click="loadData(1)">查询</el-button>
                    <el-button icon="el-icon-refresh" @click="ref()">重置</el-button>
                    <el-button icon="el-icon-plus" @click="add" type="primary">新建</el-button>

                </el-col>
            </el-row>
            <el-row class="filter">
                <el-col class="filter-label" :span="2">创建时间:</el-col>
                <el-col :span="4">
                    <el-date-picker
                            style="width: 100%"
                            type="datetimerange"
                            range-separator="~"
                            v-model="filter.time"
                            start-placeholder="开始"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd"
                            end-placeholder="结束">
                    </el-date-picker>
                </el-col>
            </el-row>

        </div>
        <el-table
                :data="data.data"
                stripe
                style="width: 100%">

            <el-table-column
                    prop="id"
                    label="角色ID">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="角色名称">
            </el-table-column>
            <el-table-column
                    prop="description"
                    label="角色描述">
            </el-table-column>
            <el-table-column
                    sortable
                    prop="status"
                    label="状态">
                <template slot-scope="scope">
                    <span>{{scope.row.status===1?'启用':'未启用'}}</span>
                </template>
            </el-table-column>
            <el-table-column
                    prop="update_time"
                    label="修改时间">
            </el-table-column>
            <el-table-column
                    label="操作">
                <template slot-scope="scope">
                    <el-button @click="detail(scope.row)" type="text" size="small">详细</el-button>
                    <el-button @click="per(scope.row)" type="text" size="small">授权</el-button>
                    <el-button @click="del(scope)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                    background
                    layout="total,prev, pager, next,jumper"
                    :page-count="data.last_page"
                    @current-change="loadData"
            >
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import api from '../../js/api/role'

    export default {
        name: "role",
        data() {
            return {
                dialog: {
                    dialogVisible: false,
                    content: "",
                },
                options: [
                    {
                        value: '1',
                        label: '正常'
                    },
                    {
                        value: '0',
                        label: '禁用'
                    }
                ],
                filter: {
                    jump_page: 1,
                    id: null,
                    status: null,
                    name: null,
                    time: null,
                    active_start: null,
                    active_end: null,
                },
                data: {},
            }
        },
        mounted() {
            this.loadData(1);
        },
        methods: {
            loadData(page) {
                this.filter.jump_page = page;
                console.log(this.filter);
                if (this.filter.time != null && this.filter.time.length === 2) {
                    this.filter.active_start = this.filter.time[0];
                    this.filter.active_end = this.filter.time[1]
                } else {
                    this.filter.active_start = null;
                    this.filter.active_end = null
                }
                api.roleList(this.filter, res => {
                    this.data = res.data.data;
                })
            },
            filterOpen() {
                this.filter.isOpen = !this.filter.isOpen;
            },
            add() {
                this.$router.push({
                    name: "settingRoleAdd",
                });
            },
            ref() {
                this.filter = {
                    jump_page: 1,
                    id: null,
                    status: null,
                    name: null,
                    time:null,
                    active_start: null,
                    active_end: null,
                };
                this.loadData(1);

            },
            del(scope) {
                let _this = this;
                this.$confirm('确认删除' + scope.row.name + '?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    api.delete({id: scope.row.id}, () => {
                        this.$message.success('删除成功');
                        _this.loadData(1);
                    })
                })
            },
            per(item) {
                this.$router.push({
                    name: "rolePer",
                    query: {
                        id: item.id,
                    },
                });
            },
            detail(item) {
                this.$router.push({
                    name: "settingRoleAdd",
                    query: {
                        id: item.id,
                    },
                });
            },
        }
    }
</script>

