import Api from './baseApi'

export default class roleApi extends Api {
    static roleList(data, callback, errorBack) {
        super.request(this.uri() + 'entry', callback, errorBack, data)
    }

    static add(data, callback, errorBack) {
        super.request(this.uri() + 'save', callback, errorBack, data)
    }

    static delete(data, callback, errorBack) {
        super.request(this.uri() + 'delete', callback, errorBack, data)
    }

    static detail(data, callback, errorBack) {
        super.request(this.uri() + 'detail', callback, errorBack, data)
    }

    static permissions(data, callback, errorBack) {
        super.request(this.uri() + 'get_role_permission', callback, errorBack, data)
    }

    static assignRolePermission(data, callback, errorBack) {
        super.request(this.uri() + 'assign_role_permission', callback, errorBack, data)
    }

    static getRolePermission(data, callback, errorBack) {
        super.request(this.uri() + 'get_role_permission', callback, errorBack, data)
    }

    static uri() {
        return super.url() + 'role/'
    }

}
